.header{
  width: inherit;
  border-bottom-style: solid;
  padding-top: 20px;
  font-family: "Lucida Grande";
}

.block{
  background-color: #4e535b;
  height: 40px;
  margin-top: 10px;
}

.name-surname{
  font-size: 25px;
  color: #4e535b;
  letter-spacing: 2px;
  display: flex;
}

.job-title{
  padding-bottom: 5px;
  font-size: 15px;
  font-family: "Lucida Grande";
  letter-spacing: 1px;
  color: #043b93;
}

.brief-summary{
  padding-top: 10px;
  font-size: 12px;
}

.personal-info{
  text-align: right;
  padding-right:50px;
  display: inline-table;
}

.personal-info ul{
  list-style-type: none;
  padding-top: 20px;
}

.personal-info ul li{
  padding-bottom: 10px;
  font-size: 10px;
  color: #4e535b;
}

.personal-info ul li i {
  padding-left: 10px;
}

.work, .skills, .education, .volunteer{
  padding: 0px 0px 0px 20px;
}

.work{
  padding-left: 20px;
}

.main_skills{
  padding-right: 50px;
  display: inline-table;
}

.title{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
  padding: 10px 0px 0px 0px;
  border-bottom-style: ridge;
}

.job_title, .top_skills, .planning,
.cloud, .school_title, .event_title{
  font-weight: bold;
  font-size: 14px;
  padding-top: 10px;
}

.company, .degree{
  font-size: 12px;
}

.start_end_date {
  font-size: 10px;
  color: #a3a8af;
}

.description {
  font-size: 12px;
  font-weight: normal;
}

.description div:before {
    content: "\2022   "
}

.soft_skills .btn-primary{
  margin: 2px;
  font-size: 10px;
  background-color: #2f66bf;
}

.soft_skills{
  margin-top: 10px;
}

